
  <div class="carousel" on:mouseenter={() => pause = true} on:mouseleave={() => pause = false}>
    <div class="slides" style={toStyle({transform: `translateX(-${currentSlide*100}%)`})}>
      {#each slides as slide, i (i)}
        <div class="slide" style={toStyle({backgroundImage: `url(${slide.background})`})}>
          <slot index={i} slide={slide} />
        </div>
      {/each}
    </div>

    {#if indicator}
      <div class="indicator">
        {#each slides as _, i}
          <span class:active={currentSlide === i} on:click={() => currentSlide = i}></span>
        {/each}
      </div>
    {/if}

    {#if arrow}
      <div class="left-control" on:click={prevSlide}>
        <ion-icon name="chevron-down-outline"></ion-icon>
      </div>
      <div class="right-control" on:click={nextSlide}>
        <ion-icon name="chevron-down-outline"></ion-icon>
      </div>
    {/if}
  </div>


<script lang="ts">import { onMount } from 'svelte';
import { toStyle } from 'src/utils';
export let slides = [];
export let arrow = true;
export let indicator = true;
export let duration = 5000;
let currentSlide = 0;
let pause = false;
const nextSlide = () => {
    currentSlide += 1;
    if (currentSlide >= slides.length)
        currentSlide = 0;
};
const prevSlide = () => {
    currentSlide -= 1;
    if (currentSlide < 0)
        currentSlide = slides.length - 1;
};
const autoplay = () => {
    setInterval(() => {
        if (pause)
            return;
        nextSlide();
    }, duration);
};
onMount(() => {
    duration > 0 && autoplay();
});
</script>

