  position: relative;
  clear: both;
  overflow: hidden;
}
.carousel:hover .left-control {
  left: 0;
  opacity: 1;
}
.carousel:hover .right-control {
  right: 0;
  opacity: 1;
}

.slides {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  transition: transform 0.4s ease-in-out;
}

.slide {
  position: relative;
  flex: none;
  width: 100%;
  padding: 48px 0;
  color: #fff;
  background: transparent center/cover no-repeat;
}

.indicator {
  position: absolute;
  right: 0;
  bottom: 8%;
  left: 0;
  display: flex;
  justify-content: center;
}
.indicator > span {
  position: relative;
  display: block;
  margin: 0 2px;
  padding: 4px;
  cursor: pointer;
  opacity: 0.2;
  transition: opacity 0.2s;
}
.indicator > span::after {
  display: block;
  width: 8px;
  height: 8px;
  background-color: #fff;
  content: "";
  border-radius: 100%;
}
.indicator > span.active {
  opacity: 0.8;
}

.left-control, .right-control {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10%;
  min-width: 48px;
  max-width: 128px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.left-control ion-icon, .right-control ion-icon {
  color: rgba(255, 255, 255, 0.6);
  font-size: 40px;
}

.left-control {
  left: -48px;
}
.left-control ion-icon {
  transform: rotateZ(90deg);
}

.right-control {
  right: -48px;
}
.right-control ion-icon {
  transform: rotateZ(-90deg);
}</style>
